.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  @media (min-width: 768px) {
    .bd-placeholder-img-lg {
      font-size: 3.5rem;
    }
  }

  .jumbotron {
    padding-top: 3rem;
    padding-bottom: 3rem;
    margin-bottom: 0;
    background-color: #fff;
  }

  @media (min-width: 768px) {
    .jumbotron {
      padding-top: 6rem;
      padding-bottom: 6rem;
    }
  }

  .jumbotron p:last-child {
    margin-bottom: 0;
  }

  .jumbotron-heading {
    font-weight: 300;
  }

  .jumbotron .container {
    max-width: 40rem;
  }

  footer {
    color: #fff !important;
    background: #081b24;
    padding: 10px;

  }

 

  footer p {
    margin-bottom: .25rem;
  }

  .modal-body img{
    margin-bottom: 10px;
  }

  .sticky-search{
    text-align: center;
    margin-bottom: 5%;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #343a40;
  }

  .jumbotron {
    background-image: url('https://wallpapercave.com/wp/wp3891762.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-color: black;
    transition: all 0.5s ease-in-out;
        border-radius: 0;
  }


  ul.buttons-wrapper {
    text-align: center;
    padding-left: 0;
  }

  ul.buttons-wrapper li {
    display: inline-block;
    margin: 20px;
  }

  a.btn.btn-dark {
    min-width: 200px;
  }

  .custom {
    background: #081b24;
    margin-right: 0px;
    max-width: 100%;
  }

  .bg-dark {
    background-color: #081b24 !important;
  }

  .modal-open .modal {
    z-index: 9999;
  }